import { Component, Fragment } from "react";
import { getMovies } from "../services/fakeMovieService";

class Movie extends Component {
  state = {
    movies: getMovies(),
  };

  renderMovies = () => {
    return (
      <Fragment>
        {this.state.movies.map((movie, index) => {
          return (
            <Fragment>
              <li
                key={movie._id}
                className="list-group-item d-flex flex-row justify-content-between"
              >
                <p className="ml-2 p-2 w-25">{movie.title}</p>
                <p className="ml-5 p-2">{movie.genre.name}</p>
                <p className="ml-5 p-2">{movie.numberInStock}</p>
                <p className="ml-5 p-2">{movie.dailyRentalRate}</p>
              </li>
              <button
                className="btn btn-danger btn-sm w-25 m-auto"
                onClick={() => this.handleDelete(movie)}
              >
                Delete
              </button>
            </Fragment>
          );
        })}
      </Fragment>
    );
  };

  handleDelete = (movie) => {
    const movies = this.state.movies.filter((mov) => mov._id !== movie._id);
    this.setState({ movies });
  };

  render() {
    if (this.state.movies.length === 0)
      return <h3 className="text-center p-5"> No Movies in the Database</h3>;
    return (
      <Fragment>
        <h3 className="text-center p-5">
          Showing {this.state.movies.length} movies in the database
        </h3>
        <ul className="list-group">
          <li className="list-group-item d-flex flex-row justify-content-between">
            <p className="ml-2 p-2 w-25">Name</p>
            <p className="ml-5 p-2 pl-5">Genre</p>
            <p className="ml-5 p-2 pl-4">Stock</p>
            <p className="ml-5 p-2 pl-4">Rent</p>
          </li>
          {this.renderMovies()}
        </ul>
      </Fragment>
    );
  }
}
export default Movie;
